import { Button, Typography } from "@mui/material";
import React from "react"; 
import { useState, useEffect } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { contactUs } from "../../actions/user";
import "./Contact.css";



const Contact = () => {

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const dispatch = useDispatch();
    const alert = useAlert();
    const {loading, message:alertMessage, error} = useSelector((state)=>state.update);
    const contactFormHandler=(e)=>{
        e.preventDefault();
        dispatch(contactUs(name,email,message));
    }

    useEffect(()=>{
        if(error){
            alert.error(error);
            dispatch({type: "CLEAR_ERRORS"})
        }
        if(alertMessage){
            alert.success(alertMessage);
            dispatch({type: "CLEAR_MESSAGE"});

        }
    },[alert, error, alertMessage, dispatch]);
    
  return <div className="contact">
    <div className="contactRightBar"></div>
        <div className="contactContainer">
            <form action="POST" className="contactContainerForm" onSubmit={contactFormHandler}>
                <Typography variant="h4">Contact Me</Typography>
                <input 
                    type="text" 
                    placeholder="Name" 
                    required
                    value={name} 
                    onChange = {(e)=>setName(e.target.value)}
                />
                <input 
                    type="email" 
                    placeholder="Email" 
                    value={email}
                    onChange = {(e)=>setEmail(e.target.value)}
                    required
                />
                <textarea 
                    rows="8" 
                    placeholder="Hi there!!!"
                    value={message}
                    onChange = {(e)=>setMessage(e.target.value)}
                    required
                ></textarea>
                <Button variant="contained" type="submit" disabled={loading}>Send</Button>
            </form>
        </div>
  </div>;
};

export default Contact;
